import {
  CustomActionModalProps,
  CustomActionParams,
  ResolvedAction,
} from '../../exports/types';
import {
  CustomActionConfig,
  DeleteActionConfig,
  UpdateActionConfig,
  DividerActionConfig,
} from './base';

export type ActionCellItemConfig =
  | UpdateActionConfig
  | CustomActionConfig
  | DeleteActionConfig;

export interface ActionCellPrimaryAction {
  item: ActionCellItemConfig;
}

export interface ActionCellSecondaryActions {
  items: (ActionCellItemConfig | DividerActionConfig)[];
  inlineCount?: number;
  inlineAlwaysVisible?: boolean;
}

export interface ActionCellConfig {
  primaryAction?: ActionCellPrimaryAction;
  secondaryActions?: ActionCellSecondaryActions;
}

export interface ActionCellActionParams {
  item: any;
}

export type CustomActionCellActionParams =
  CustomActionParams<ActionCellActionParams>;

export type CustomActionCellActionResolver = (
  params: CustomActionCellActionParams,
) => ResolvedAction;

export interface CustomActionCellActionModalProps
  extends CustomActionModalProps {
  actionParams: ActionCellActionParams;
}
